.btn,
.instruction-link {
  font-family: 'Shippori Mincho', serif;
  font-weight: 800;
  text-align: center;
  padding: 5px 10px;
  color: $text-color;
  text-shadow: 1px -1px 1px black;
  font-size: 2.1rem;
}

.screen .btn {
  width: 295px;
  border: 3px solid $text-color;
  box-shadow: inset 0 0 20px $text-color;
  transition: all 0.6s;
  @media (hover: hover) {
    &:hover {
      box-shadow: inset 15px 15px 40px $text-color;
    }
  }

  @media (max-width: 768px) {
    width: 200px;
  }
}

.game-control .btn {
  font-size: 1.8rem;
  width: 200px;
  @media (max-width: 768px) {
    width: 150px;
  }
  @media (max-width: 690px) {
    width: 100px;
  }
}

.sound-btn {
  position: relative;
  grid-area: sound;
  width: 40px;
  height: 40px;
  background-image: url('../assets/images/loon-icon.svg');
  background-position: center;
}

@media (hover: hover) {
  .instruction-link,
  .header .btn {
    transition: all 0.5s;
    &:hover {
      color: #eb7b95;
    }
  }
}
